import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { JwtModule } from '@auth0/angular-jwt';
import { MaterialModule } from './shared';
import { EsportService } from './shared';
import { AuthGuard } from './shared/guards/auth.guard';

import { AppRoutingModule } from './app.routing';
import { LayoutModule } from './layout/layout.module';
import { AppComponent } from './app.component';

import { FetchTeamsComponent } from './esport/fetch-teams/fetch-teams.component';
import { AddTeamComponent } from './esport/add-team/add-team.component';
import { EditTeamsComponent } from './esport/edit-teams/edit-teams.component';
import { ProfileComponent } from './esport/profile/profile.component';

const ESPORT_COMPONENTS = [
  FetchTeamsComponent,
  AddTeamComponent,
  EditTeamsComponent
];

@NgModule({
  declarations: [
    AppComponent,
    ...ESPORT_COMPONENTS,
    ProfileComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    AppRoutingModule,
    LayoutModule,
		FormsModule, ReactiveFormsModule,
		MaterialModule,
    JwtModule.forRoot({
      config: {
        tokenGetter: function  tokenGetter() {
          return localStorage.getItem('access_token');
        },
        whitelistedDomains: ['https://localhost:3000', 'https://tmf-esport.herokuapp.com'],
        // blacklistedRoutes: ['http://localhost:3000/auth/login']
      }
    })
  ],
  providers: [EsportService, AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }
