import { Injectable } from '@angular/core';
import { Observable, throwError } from "rxjs";
import { HttpClient, HttpHeaders, HttpErrorResponse } from "@angular/common/http";
import { tap } from 'rxjs/operators';
import { User } from '../interfaces/user.interface';

@Injectable({
	providedIn: 'root'
})

export class AuthService {

	baseURL: string = 'https://tmf-esport.herokuapp.com/users';
	// baseURL: string = 'http://localhost:8080/users';
  authToken: any;
  user: any;

	constructor(private HTTP: HttpClient) { }

  loadToken() {
    this.authToken = localStorage.getItem('token');
  }

  storeUserData(token, user) {
		localStorage.setItem('token', token);
		localStorage.setItem('user', JSON.stringify(user));
		this.authToken = token;
		this.user = user;
	}

	// REGISTER newUser
  registerUser(name: string, username:string, email: string, password: string, userAvatar: File)
	: Observable<any> {
    let formData: any = new FormData();
    formData.append("name", name);
    formData.append("username", username);
    formData.append("email", email);
    formData.append("password", password);
    formData.append("avatar", userAvatar);

    return this.HTTP.post<User>(`${this.baseURL}/register`, formData, {
      reportProgress: true,
      observe: "events"
    });
  }

  loginUser(user: any)
	: Observable<any> {
    let headers: any = new HttpHeaders();
    headers.append('Content-Type', 'application/json');

    return this.HTTP.post<any>(`${this.baseURL}/login`, {headers: headers});
  }

  // LOGIN user
  authenticateUser(user): Observable<any> {
		let headers: any = new HttpHeaders();
		headers.append('Content-Type', 'application/json');
		return this.HTTP.post<{token: string, user: string}>(this.baseURL + '/login', user, {headers: headers})
      .pipe(tap(res => {
        console.log(user.username + ' logged in.');
      }))
	}

  // LOGGEDIN status
  loggedIn(): boolean {
    return localStorage.getItem('token') !==  null;
  }

  // LOGOUT
  logoutUser() {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
  }

  // ERROR handling
  errorMgmt(error: HttpErrorResponse) {
    let errorMessage = "";
    if (error.error instanceof ErrorEvent) {
      errorMessage = error.error.message; // Get client-side error
    } else {
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`; // Get server-side error
    }
    console.log(errorMessage);
    return throwError(errorMessage);
  }

}
